import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import style from "../../StoreManager/StoreManager.module.css";
import { useState } from "react";
import StaffSiteActivityCardMenu from "./StaffSiteActivityCardMenu";
import FormFileView from "../../../Ui/Input/FormFileView";
const StaffSiteActivityCard = ({ pso, onRefetch, sid }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);
  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <div
        className={style.smc_card}
        style={{
          alignItems: "flex-start",
        }}
      >
        <div className={style.smc_card_contatiner}>
          <div className={style.smc_card_contatiner}>
            <div className={style.smc_card_contatiner_inner}>
              <h6>
                {t("title")} {": "}
                {pso?.title ?? ""}
              </h6>
              <p>
                {t("sr_no_only")} {": "}
                {pso?.srn ?? ""}
              </p>
              <p>
                {t("department")} {": "}
                {pso?.department?.dName ?? ""}
              </p>
              <p>
                {t("student")} {": "}
                {`${pso?.student?.studentFirstName ?? ""} ${
                  pso?.student?.studentMiddleName ?? ""
                } ${pso?.student?.studentLastName ?? ""}`}
              </p>
              <p>
                {t("class")} {": "}
                {`${pso?.classes?.className ?? ""} -  ${
                  pso?.classes?.classTitle ?? ""
                }`}
              </p>
              <p>
                {t("subject_only_1")} {": "}
                {pso?.subject ?? ""}
              </p>
              <p>
                {t("guide_name")} {": "}
                {pso?.guide_name ?? ""}
              </p>

              {pso?.link ? (
                <p>
                  {t("link")} {": "}
                  <a
                    href={pso?.link}
                    target="_blank"
                    rel="noopener noreferrer"
                    style={{
                      color: "#0942a6",
                    }}
                  >
                    {pso?.link}
                  </a>
                </p>
              ) : null}
              {pso?.attach ? (
                <FormFileView
                  label={`${t("attachment")} :`}
                  value={{
                    key: pso?.attach,
                  }}
                  customFormInputContainer={{
                    marginBlock: "0",
                  }}
                />
              ) : null}

              <p
                style={{
                  whiteSpace: "break-spaces",
                }}
              >
                {t("abstract")} {": "}
                {pso?.abstract ?? ""}
              </p>
            </div>
          </div>
        </div>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onToggle}
        />
      </div>
      {open && (
        <StaffSiteActivityCardMenu
          onClose={onToggle}
          menuData={pso}
          onRefetch={onRefetch}
          sid={sid}
        />
      )}
    </>
  );
};

export default StaffSiteActivityCard;
