import React, { useCallback, useEffect, useState } from "react";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../FinanceManager/FinanceManager.module.css";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import { useStaffAllProjectList } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import StaffSiteProjectCard from "./StaffSiteProjectCard";
import AddStaffSiteProject from "./AddStaffSiteProject";
const StaffSiteProject = ({ sid, instituteId }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const {
    staffAllProjectList,
    staffAllProjectListLoading,
    staffAllProjectListRefetch,
  } = useStaffAllProjectList({
    data: {
      did: sid,
      page: page,
      limit: 10,
      search: search,
      flow: "STAFF",
    },
    skip: !sid,
  });

  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (staffAllProjectList?.list?.length) {
      let obj = nextPage(staffAllProjectList?.list?.length);
      setState(() => obj);
    }
  }, [staffAllProjectList?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    staffAllProjectListRefetch();
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
              />
            </div>
          </section>
          <CreateButton
            label="add"
            customStyle={{
              paddingBlock: "0.4rem",
              margin: "0",
            }}
            onAction={onToggle}
          />
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />

      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0.3rem",
        }}
      >
        {staffAllProjectList?.list?.map((pso, index) =>
          staffAllProjectList?.list?.length === index + 1 ? (
            <div key={pso?._id} ref={ref}>
              <StaffSiteProjectCard pso={pso} onRefetch={onRefetch} sid={sid} />
            </div>
          ) : (
            <StaffSiteProjectCard
              pso={pso}
              key={pso?._id}
              onRefetch={onRefetch}
              sid={sid}
            />
          )
        )}

        {staffAllProjectListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          staffAllProjectList?.list?.length === 0 && (
            <EmptyMessage
              title="No data found related this search."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        ) : (
          staffAllProjectList?.list?.length === 0 && (
            <EmptyMessage
              title="No data."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        )}
      </UserMemberContentWrapper>
      {add && (
        <AddStaffSiteProject
          onClose={onToggle}
          sid={sid}
          onRefetch={onRefetch}
          instituteId={instituteId}
        />
      )}
    </>
  );
};

export default StaffSiteProject;
