import RectangleTabWrapper from "../../Ui/Tab/Rectangle/RectangleTabWrapper";
import RectangleTabCard from "../../Ui/Tab/Rectangle/RectangleTabCard";
import { useLocation, useParams, useSearchParams } from "react-router-dom";

const StaffSiteActivityProjectProfile = () => {
  const params = useParams();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  return (
    <>
      <RectangleTabWrapper
        customStyle={{
          borderTopLeftRadius: "6px",
          borderTopRightRadius: "6px",
        }}
      >
        {tabList.map((tab) => (
          <RectangleTabCard
            key={tab?.id}
            label={tab.label}
            isLink
            isActive={
              tab.id === "activity" && !searchParams.get("type")
                ? true
                : searchParams.get("type") === tab.id
                ? true
                : false
            }
            parentState={getQuery.state}
            url={`/q/${params.username}/member?a=activityproject${tab.urlKey}`}
            isAuthorized
          />
        ))}
      </RectangleTabWrapper>
    </>
  );
};

export default StaffSiteActivityProjectProfile;

const tabList = [
  {
    label: "activity",
    id: "activity",
    urlKey: "",
  },
  {
    label: "projects",
    id: "project",
    urlKey: "&type=project",
  },
];
