import React, { Suspense, lazy, useEffect } from "react";
import StaffMemberConfig from "../Mainbody/StaffMemberConfig";
import { useLocation } from "react-router-dom";
import { useStaffDesignationDataQuery } from "../../../services/joining/joinApi";
import QvipleLoader from "../../../optimized/Loader/QvipleLoader";
import StaffSiteActivityProject from "../../../optimized/UserStaffMember/StaffSiteActivityProject/StaffSiteActivityProject";

const ViewProfile = lazy(() => import("../Mainbody/ViewProfile/ViewProfile"));
const Complaints = lazy(() => import("../Mainbody/Complaints/Complaints"));
const Transfers = lazy(() => import("../Mainbody/Transfers/Transfers"));
const SwitchAccount = lazy(() =>
  import("../Mainbody/SwitchAccount/SwitchAccount")
);
const StudentMessageHistory = lazy(() =>
  import(
    "../../../optimized/UserStaffMember/FinanceManager/FinanceAllStudent/StudentMessageHistory/StudentMessageHistory"
  )
);
const StaffLeave = lazy(() =>
  import("../../../optimized/UserStaffMember/StaffLeave/StaffLeave")
);
const IndividualGoodsStore = lazy(() =>
  import(
    "../../../optimized/UserStaffMember/IndividualGoodsStore/IndividualGoodsStore"
  )
);
const StaffSelfAccessConfig = () => {
  const location = useLocation();
  const { data: staffDesignationData, refetch: staffDesignationDataRefetch } =
    useStaffDesignationDataQuery(location?.state?.sid, {
      skip: !location?.state?.sid,
    });

  useEffect(() => {
    if (location?.state?.sid) staffDesignationDataRefetch();
  }, [location?.state?.sid, staffDesignationDataRefetch]);

  // console.info("staffDesignationData", staffDesignationData);
  return (
    <StaffMemberConfig
      index={
        location?.search?.substring(3) === "profile"
          ? "20"
          : location?.search?.substring(3) === "complaint"
          ? "21"
          : location?.search?.substring(3) === "leave"
          ? "22"
          : location?.search?.substring(3) === "transfer"
          ? "23"
          : location?.search?.substring(3) === "student-message"
          ? "60"
          : location?.search?.substring(3, 8) === "store"
          ? "61"
          : location?.search?.substring(3, 18) === "activityproject"
          ? "101"
          : "24"
      }
      sid={location?.state?.sid ?? ""}
    >
      <Suspense fallback={<QvipleLoader />}>
        {location?.search?.substring(3) === "profile" && (
          <ViewProfile staffData={location?.state?.sid ?? ""} />
        )}
        {location?.search?.substring(3) === "complaint" && (
          <Complaints staffId={location?.state?.sid ?? ""} />
        )}
        {location?.search?.substring(3, 8) === "leave" && (
          <StaffLeave sid={location?.state?.sid ?? ""} />
        )}
        {location?.search?.substring(3) === "transfer" && (
          <Transfers staffId={location?.state?.sid ?? ""} />
        )}
        {location?.search?.substring(3) === "switch" && <SwitchAccount />}
        {location?.search?.substring(3) === "student-message" && (
          <StudentMessageHistory
            instituteId={staffDesignationData?.staff?.institute?._id}
            financeId={
              staffDesignationData?.staff?.institute?.financeDepart?.[0]
            }
            headId={location?.state?.sid}
            accessAs={
              staffDesignationData?.staff?.current_designation
                ? staffDesignationData?.staff?.current_designation
                : // : staffDesignationData?.staff?.recentDesignation
                  ""
            }
          />
        )}
        {location?.search?.substring(3, 8) === "store" && (
          <IndividualGoodsStore
            staffId={location?.state?.sid}
            stid={staffDesignationData?.staff?.institute?.storeDepart?.[0]}
          />
        )}
        {location?.search?.substring(3, 18) === "activityproject" && (
          <StaffSiteActivityProject
            instituteId={staffDesignationData?.staff?.institute?._id}
          />
        )}
      </Suspense>
    </StaffMemberConfig>
  );
};

export default StaffSelfAccessConfig;
