import { useTranslation } from "react-i18next";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import { useStaffRemoveProject } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";

const StaffSiteProjectCardMenu = ({ sid, onClose, menuData, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [staffRemoveProject] = useStaffRemoveProject();

  const onDelete = () => {
    if (menuData?._id && menuData?.department?._id) {
      setDisabled((pre) => !pre);
      staffRemoveProject({
        // did: menuData?.department,
        did: menuData?.department?._id,
        bodyParameter: {
          pid: menuData?._id,
          sid: sid,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        <h6 onClick={onDelete}>{t("delete")}</h6>
      </ModalMenu>
    </>
  );
};

export default StaffSiteProjectCardMenu;
