import { useState } from "react";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormInput from "../../../Ui/Input/FormInput";
import QvipleLoader from "../../../Loader/QvipleLoader";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useStaffAddProject } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/Income/Income.module.css";
import { useFileUpload } from "../../../Hooks/ApiHooks/Auth/auth-api";
import FormFile from "../../../Ui/Input/FormFile";
import InstituteStudent from "../../InstituteStudent/InstituteStudent";
import InstituteClass from "../../InstituteClass/InstituteClass";
import styleEdit from "../../../../Profile/Institute/Pages/Site/SiteEdit.module.css";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import InstituteMixedDepartment from "../../InstituteMixedDepartment/InstituteMixedDepartment";
import FormInputView from "../../../Ui/Input/FormInputView";
import FormTextarea from "../../../Ui/Input/FormTextarea";

const AddStaffSiteProject = ({ onClose, onRefetch, sid, instituteId }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    srn: "",
    title: "",
    studentData: "",
    classesData: "",
    subject: "",
    guide_name: "",
    linkUrl: "",
    attach: "",
    add_as: "DEPT",
    abstract: "",
    department: "",
  });

  const [staffAddProject] = useStaffAddProject();
  const [fileUpload] = useFileUpload();

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            attach: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onAdd = () => {
    let validation = requiredField(state, ["add_as"]);
    if (existingRequiredField(validation) && sid && state?.department?._id) {
      setDisabled((pre) => !pre);
      staffAddProject({
        did: state?.department?._id,
        bodyParameter: {
          ...state,
          link: state.linkUrl,
          student: state?.studentData?._id,
          classes: state.classesData?._id,
          sid: sid,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };
  const onSelectCls = (assing) => {
    setState((prev) => ({
      ...prev,
      classesData: assing,
      add_as: "",
    }));
  };
  const onSelectStudent = (assing) => {
    setState((prev) => ({
      ...prev,
      studentData: assing,
      add_as: "",
    }));
  };

  const onSelectDepartmentOp = (assing) => {
    setState((prev) => ({
      ...prev,
      department: assing,
      add_as: "",
    }));
  };
  // console.info("sdkfbgnd", state);
  return (
    <>
      {state.add_as === "DEPT" ? (
        <InstituteMixedDepartment
          onSelectStaff={onSelectDepartmentOp}
          title={t("select_department")}
          onClose={() =>
            setState((prev) => ({
              ...prev,
              add_as: "",
            }))
          }
          selectedId={state?.department?._id}
          instituteId={instituteId}
        />
      ) : state.add_as === "CLS" ? (
        <InstituteClass
          onSelectStaff={onSelectCls}
          title={t("select_class")}
          onClose={() =>
            setState((prev) => ({
              ...prev,
              add_as: "",
            }))
          }
          selectedId={state.studentData?._id}
          instituteId={instituteId}
        />
      ) : state.add_as === "STU" ? (
        <InstituteStudent
          onSelectStudent={onSelectStudent}
          title={t("select_student")}
          onClose={() =>
            setState((prev) => ({
              ...prev,
              add_as: "",
            }))
          }
          selectedId={state.classesData?._id}
          instituteId={instituteId}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("add_project")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInputView
                label={`${t("selected_department")}: `}
                value={state.department?.dName ?? ""}
              />
              <FormInput
                label={t("sr_no_only")}
                placeholder={t("sr_no_placeholder")}
                name={"srn"}
                value={state.srn}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["srn"]}
              />
              <FormInput
                label={t("title")}
                placeholder={t("title_placeholder")}
                name={"title"}
                value={state.title}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["title"]}
              />

              <div
                className={styleEdit.leading_person_container}
                onClick={() =>
                  setState((prev) => ({
                    ...prev,
                    add_as: "STU",
                  }))
                }
                style={{
                  marginTop: "1rem",
                  width: "100%",
                }}
              >
                <img
                  src={
                    state?.studentData?.studentProfilePhoto
                      ? `${imageShowUrl}/${state?.studentData.studentProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                  alt="avatar"
                />
                <div className={styleEdit.leading_person_text}>
                  <h6>
                    {state?.studentData
                      ? `${state?.studentData?.studentFirstName ?? ""} ${
                          state?.studentData?.studentMiddleName ?? ""
                        } ${state?.studentData?.studentLastName ?? ""}`
                      : t("select_student")}
                  </h6>
                  <p>
                    {t("search_joined_list")}{" "}
                    {errorField["studentData"] && (
                      <span style={{ color: "red", marginLeft: "15px" }}>
                        * {t("form_require_label")}
                      </span>
                    )}
                  </p>
                </div>
              </div>

              <div
                className={styleEdit.leading_person_container}
                onClick={() =>
                  setState((prev) => ({
                    ...prev,
                    add_as: "CLS",
                  }))
                }
                style={{
                  marginTop: "1rem",
                  width: "100%",
                }}
              >
                <img
                  src={
                    state?.classesData?.staffProfilePhoto
                      ? `${imageShowUrl}/${state?.classesData.staffProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                  alt="avatar"
                />
                <div className={styleEdit.leading_person_text}>
                  <h6>
                    {state?.classesData?.className
                      ? `${state?.classesData?.className ?? ""} - 
                    ${state?.classesData?.classTitle}`
                      : t("select_class")}
                  </h6>
                  <p>
                    {t("select_class")}{" "}
                    {errorField["studentData"] && (
                      <span style={{ color: "red", marginLeft: "15px" }}>
                        * {t("form_require_label")}
                      </span>
                    )}
                  </p>
                </div>
              </div>

              <FormInput
                label={t("subject_only_1")}
                placeholder={t("subject_placeholder")}
                name={"subject"}
                value={state.subject}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["subject"]}
              />

              <FormInput
                label={t("guide_name")}
                placeholder={t("guide_name_placeholder")}
                name={"guide_name"}
                value={state.guide_name}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["guide_name"]}
              />

              <FormInput
                label={t("link")}
                placeholder={t("link_placeholder")}
                name={"linkUrl"}
                value={state.linkUrl}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["linkUrl"]}
              />
              <FormFile
                label={t("add_attachment")}
                name={"add_attachment"}
                value={
                  state.attach
                    ? {
                        key: state.attach,
                        name: "View",
                      }
                    : ""
                }
                onChange={onFileUpload}
                errorShow={errorField["attach"]}
              />
              <FormTextarea
                label={t("abstract")}
                placeholder={t("abstract_placeholder")}
                name="abstract"
                type="text"
                value={state.abstract}
                onChange={onChange}
                errorShow={errorField["abstract"]}
              />
              <CreateButton label="add" onAction={onAdd} />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default AddStaffSiteProject;
