import { useTranslation } from "react-i18next";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import { useStaffRemoveActivity } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";

const StaffSiteActivityCardMenu = ({ onClose, menuData, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [staffRemoveActivity] = useStaffRemoveActivity();

  const onDelete = () => {
    if (menuData?._id) {
      setDisabled((pre) => !pre);
      staffRemoveActivity({
        // did: menuData?.department,
        acid: menuData?._id,
        bodyParameter: null,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        <h6 onClick={onDelete}>{t("delete")}</h6>
      </ModalMenu>
    </>
  );
};

export default StaffSiteActivityCardMenu;
