import { useLocation, useSearchParams } from "react-router-dom";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import StaffSiteActivityProjectProfile from "./StaffSiteActivityProjectProfile";
import StaffSiteProject from "./StaffSiteProject/StaffSiteProject";
import StaffSiteActivity from "./StaffSiteActivity/StaffSiteActivity";

const StaffSiteActivityProject = ({ instituteId }) => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <StaffSiteActivityProjectProfile />
        </UserMemberContentWrapper>
        {searchParams.get("type") === "project" ? (
          <StaffSiteProject
            sid={getQuery.state?.sid}
            instituteId={instituteId}
          />
        ) : (
          <StaffSiteActivity
            sid={getQuery.state?.sid}
            instituteId={instituteId}
          />
        )}
      </UserMemberContainerWrapper>
    </>
  );
};

export default StaffSiteActivityProject;
