import { useState } from "react";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormInput from "../../../Ui/Input/FormInput";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useStaffAddActivity } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/Income/Income.module.css";
import InstituteMixedDepartment from "../../InstituteMixedDepartment/InstituteMixedDepartment";
import FormInputView from "../../../Ui/Input/FormInputView";
import CustomSelect from "../../../Ui/Select/CustomSelect";

const AddStaffSiteActivity = ({ onClose, onRefetch, sid, instituteId }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    activity_name: "",
    activity_type: "",
    activity_department: "",
    mou: "",
    add_as: "DEPT",
  });

  const [staffAddActivity] = useStaffAddActivity();

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onAdd = () => {
    let validation = requiredField(state, ["add_as", "mou"]);
    if (
      existingRequiredField(validation) &&
      sid &&
      state?.activity_department?._id
    ) {
      setDisabled((pre) => !pre);
      staffAddActivity({
        did: state?.activity_department?._id,
        bodyParameter: {
          ...state,
          activity_department: state.activity_department?._id,
          mou: state?.mou ? state.mou : null,
          staff_id: sid,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };
  const onSelectCategroy = (assing) => {
    setState((prev) => ({
      ...prev,
      activity_type: assing,
    }));
  };

  const onSelectDepartmentOp = (assing) => {
    setState((prev) => ({
      ...prev,
      activity_department: assing,
      add_as: "",
    }));
  };
  // console.info("sdkfbgnd", state);
  return (
    <>
      {state.add_as === "DEPT" ? (
        <InstituteMixedDepartment
          onSelectStaff={onSelectDepartmentOp}
          title={t("select_department")}
          onClose={() =>
            setState((prev) => ({
              ...prev,
              add_as: "",
            }))
          }
          selectedId={state?.department?._id}
          instituteId={instituteId}
        />
      ) : state.add_as === "MOU" ? (
        <InstituteMixedDepartment
          onSelectStaff={onSelectDepartmentOp}
          title={t("select_department")}
          onClose={() =>
            setState((prev) => ({
              ...prev,
              add_as: "",
            }))
          }
          selectedId={state?.department?._id}
          instituteId={instituteId}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("add_activity")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInputView
                label={`${t("selected_department")}: `}
                value={state.activity_department?.dName ?? ""}
              />
              <FormInput
                label={t("activity_name")}
                placeholder={t("activity_name_placeholder")}
                name={"activity_name"}
                value={state.activity_name}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["activity_name"]}
              />
              <CustomSelect
                selectLabel={t("types_of_activities")}
                selectedValue={t("types_of_activities_placeholder")}
                type={"text"}
                onClick={onSelectCategroy}
                options={mouCategory}
                defalutValue={state.activity_type}
                errorShow={errorField["activity_type"]}
              />

              <CreateButton
                label="select_mou"
                onAction={onAdd}
                customStyle={{
                  width: "fit-content",
                  margin: "1rem 0",
                  padding: "0.4rem 1rem",
                }}
              />
              <FormInputView
                label={`${t("selected_mou")}: `}
                value={state.department?.dName ?? ""}
              />

              <CreateButton label="add" onAction={onAdd} />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default AddStaffSiteActivity;

const mouCategory = [
  "Training Program",
  "Orientation Program",
  "Seminar",
  "Webinar",
  "Workshop",
  "Certificate Course",
  "Credit Course",
  "Expert Guest Lecture",
  "Field/Study Tour Visits",
  "Hands On Traning Program",
  "State Level Conferance",
  "National Level Conferance",
  "International Level Conferance",
  "Days Celebration",
  "Outrich Program",
  "Social Program",
  "Extension Activities",
  "Exhibitions",
  "Quize Compitions",
  "Any Other",
];
